/**
 * tabs
 */
import { useTags } from '@/hooks';
import Tab, { IProps } from './Tab';

const Tabs = (): JSX.Element => {
    const { tags } = useTags();

    return (
        <ul className="typeTabs content_box">
            {tags.map(
                (el: IProps): JSX.Element => {
                    return <Tab key={String(el.id)} color={el.color} className="tab_item" name={el.name} />;
                }
            )}
        </ul>
    );
};

export default Tabs;
