/**
 * app
 */
import AppRouter from '@/router';
import AppContext from '@/context/AppContext';
import { useUserInfo } from '@/hooks';

const App = (): JSX.Element => {
    const { userInfo } = useUserInfo();

    return (
        <AppContext.Provider value={userInfo}>
            <AppRouter />
        </AppContext.Provider>
    );
};

export default App;
