/* eslint-disable react/no-danger */
/**
 * article item
 */
import { FIX_AUTHOR } from '@/enums/local';
import { useHistory } from 'react-router-dom';

export interface IProps {
    index: number;
    title: string;
    tags: string[];
    author: string;
    createdAt: string;
    body: string;
}

const SectionItem = (props: IProps): JSX.Element => {
    const { title, tags, author, createdAt, body, index } = props;
    const history = useHistory();

    const jumpDetails = (id: number) => {
        history.push(`/article/${id}/details`);
    };
    return (
        <div
            className="section_box"
            role="button"
            tabIndex={index}
            onClick={() => jumpDetails(index)}
            onKeyDown={() => jumpDetails(index)}
        >
            <h2 className="article_title">{title}</h2>
            <ul className="article_tags_and_author">
                <li className="article_author">{`${FIX_AUTHOR}${author}`}</li>
                <li className="article_created">{createdAt}</li>
                <li className="article_tags">
                    {tags.map((el) => (
                        <span key={el}>{el}</span>
                    ))}
                </li>
            </ul>
            <p className="article_body">{body}</p>
        </div>
    );
};

export default SectionItem;
