/* eslint-disable @typescript-eslint/no-explicit-any */
/**
 * github hooks
 */

import { useState, useEffect } from 'react';
import { toArray } from 'lodash';
import { IuserInfo } from '@/interfaces';
import github from '../apis/github';

export const DEFAULT_USER_INFO = { bio: '', blog: '', location: '', name: '', company: '' };

export const useUserInfo = (): { userInfo: IuserInfo } => {
    const [userInfo, setUserInfo] = useState(DEFAULT_USER_INFO);
    useEffect(() => {
        const fetchUserInfo = async () => {
            const data = await github.user();
            setUserInfo({ ...data });
        };

        fetchUserInfo();
    }, []);

    return {
        userInfo,
    };
};

export const useIssues = (): any => {
    const [issues, setIssues] = useState([]) as any[];
    useEffect(() => {
        const fetchUserInfo = async () => {
            const data = await github.issues();
            setIssues(toArray(data));
        };

        fetchUserInfo();
    }, []);

    return {
        issues,
    };
};

export const useTags = (): any => {
    const [tags, setTags] = useState([]) as any[];
    useEffect(() => {
        const fetchUserInfo = async () => {
            const data = await github.tags();
            setTags(toArray(data));
        };

        fetchUserInfo();
    }, []);

    return {
        tags,
    };
};
