/**
 * github urls
 */
import createURLTemplate from '@/utils/url';

// user info
export const getUserInfoURL = createURLTemplate(`/users/${process.env.REACT_APP_GITHUB_USERNAME}`);

// issues
export const getIssueURL = createURLTemplate(`/repos/${process.env.REACT_APP_GITHUB_USERNAME}/blog/issues`);

// tags
export const getReposTagsURL = createURLTemplate(`/repos/${process.env.REACT_APP_GITHUB_USERNAME}/blog/labels`);
