/**
 * @file url utils
 *
 * */

import utpl, { URITemplate } from 'uri-templates';

/**
 * create url template
 *
 *
 * @return {Object}
 */
const createURLTemplate = (url: string, prefix = process.env.REACT_APP_GITHUB_API_PIX): URITemplate => {
    return utpl(`${prefix}${url}`);
};

export default createURLTemplate;
