/* eslint-disable no-console */
/**
 * fetch
 */

import axios from 'axios';

const AUTH_TOKEN = `${process.env.REACT_APP_GITHUB_TOKEN_TOP}${process.env.REACT_APP_GITHUB_TOKEN_BOTTOM}`;

const fetch = axios.create({
    baseURL: process.env.REACT_APP_AXIOS_BASE_URL,
    timeout: Number(process.env.REACT_APP_AXIOS_MAX_TIMEOUT),
});

// eslint-disable-next-line dot-notation
fetch.defaults.headers.common['Authorization'] = AUTH_TOKEN;

// 请求拦截器
fetch.interceptors.request.use(
    (config) => {
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// 添加响应拦截器
fetch.interceptors.response.use(
    (response) => {
        // 直接返回data，减低层级
        const { data } = response;
        // console.log(data);
        return data;
    },
    (error) => {
        console.log(error);
        return Promise.reject(error);
    }
);

export default fetch;
