/**
 * navs
 */
import { NAV_CONFIGS, IConfig } from '@/enums';

const Navs = (): JSX.Element => {
    return (
        <nav className="navs">
            {NAV_CONFIGS.map((el: IConfig) => {
                return (
                    <p key={el.icon} className="nav_item">
                        <a href={el.link} target="_blank" rel="noreferrer">
                            <i className={`iconfont ${el.icon}`} />
                        </a>
                    </p>
                );
            })}
        </nav>
    );
};

export default Navs;
