/* eslint-disable @typescript-eslint/no-explicit-any */
/**
 * github service
 */
import fetch from '@/utils/fetch';
import { getUserInfoURL, getIssueURL, getReposTagsURL } from '@/urls';

const getUserInfoService = (): any => {
    return fetch.get(getUserInfoURL.template);
};

const getIssueService = (): any => {
    return fetch.get(getIssueURL.template);
};

const getTagsService = (): any => {
    return fetch.get(getReposTagsURL.template);
};

const github = {
    user: getUserInfoService,
    issues: getIssueService,
    tags: getTagsService,
};

export default github;
