/**
 * tab item
 */
export interface IProps {
    id?: number;
    name: string | undefined;
    className: string | undefined;
    color: string;
}

const Tab = (props: IProps): JSX.Element => {
    const { name, className, color } = props;
    return (
        <li style={{ color: `#${color}` }} className={className} key={name}>
            {name}
        </li>
    );
};

export default Tab;
