/* eslint-disable camelcase */
/**
 * article list
 */
import { useIssues } from '@/hooks';
import { cloneElement } from 'react';
import SectionItem, { IProps } from './Item';

interface IPorpsLost extends IProps {
    created_at: string;
}

const ArticleList = (): JSX.Element => {
    const { issues } = useIssues();
    console.log('issues', issues);
    return (
        <section className="section">
            {issues.map(
                (el: IPorpsLost, index: number): JSX.Element => {
                    return cloneElement(
                        <SectionItem
                            index={index}
                            title={el.title}
                            tags={['闲聊']}
                            author={process.env.REACT_APP_GITHUB_USERNAME as string}
                            createdAt={el.created_at}
                            body={el.body}
                        />,
                        { key: `SectionItem-${el}` }
                    );
                }
            )}
        </section>
    );
};

export default ArticleList;
